@font-face {
  font-family: "SF Pro Display";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("../../fonts/SF-Pro-Display-Regular.woff2") format("woff2"), url("../../fonts/SF-Pro-Display-Regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("../../fonts/SF-Pro-Display-Medium.woff2") format("woff2"), url("../../fonts/SF-Pro-Display-Medium.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Display";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Display"), url("../../fonts/SF-Pro-Display-Semibold.woff2") format("woff2"), url("../../fonts/SF-Pro-Display-Semibold.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("../../fonts/SFProText-Regular.woff2") format("woff2"), url("../../fonts/SFProText-Regular.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("../../fonts/SFProText-Medium.woff2") format("woff2"), url("../../fonts/SFProText-Medium.woff") format("woff");
}
@font-face {
  font-family: "SF Pro Text";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("SF Pro Text"), url("../../fonts/SFProText-Semibold.woff2") format("woff2"), url("../../fonts/SFProText-Semibold.woff") format("woff");
}
*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page {
  background-color: white;
}

.wrapper {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

.container {
  position: relative;
  z-index: 0;
  box-sizing: border-box;
  margin: 0 auto;
  min-width: 900px;
  max-width: 1440px;
  min-height: 800px;
  width: 100%;
  height: auto;
  font-family: "Inter", sans-serif;
  background-color: #F5F9FF;
  border-radius: 20px;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 400;
  color: #5F6B77;
}

.container::after {
  content: "";
  z-index: -1;
  position: absolute;
  max-width: 41%;
  width: 458px;
  height: 100%;
  top: 0;
  right: 0;
  background-color: #ECF2FE;
  background: linear-gradient(45deg, #506CCF 0%, #3453C2 100%);
  border-radius: 0 20px 20px 0;
}

.page-main {
  padding: 9.2rem 2.5rem 0 8.375rem;
  overflow: hidden;
  z-index: 10;
  background-position: top 447px right 403px, top 72px right 151px, top 482px right 725px, top 645px right 256px, top 13px right 550px, top 210px right 365px, right -1px top 58px;
  background-repeat: no-repeat, no-repeat, no-repeat, no-repeat, no-repeat, no-repeat, no-repeat, no-repeat;
  background-size: auto, auto, auto, auto, auto, auto, auto;
  background-image: url("../../img/rectangle.png"), url("../../img/man-2.png"), url("../../img/man.png"), url("../../img/ellipse-orange.png"), url("../../img/ellipse-purple.png"), url("../../img/ellipse-red.png"), url("../../img/diagrams.svg");
}

.button {
  font-family: "SF Pro Text", sans-serif;
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 500;
  color: white;
  padding: 0.75rem 1.5625rem 0.75rem 1.5rem;
  width: 127px;
  height: 46px;
  background-color: #49A1F1;
  border-radius: 10px;
  cursor: pointer;
}
.button--purple {
  background-color: #506CCF;
}
.button--transparent {
  background-color: transparent;
  border: 1px solid #E9EAF0;
}
.button--transparent {
  background-color: transparent;
  border: 1px solid #E9EAF0;
}
.button--bold-border {
  border: 2px solid #5F6B77;
}
.button:hover {
  opacity: 0.8;
}

.heading {
  font-family: "SF Pro Display", sans-serif;
  font-size: 3rem;
  line-height: 3.625rem;
  font-weight: 600;
  color: #08123A;
  max-width: 100%;
  width: max-content;
  padding: 0;
  margin: 0;
}

.page-header {
  padding: 1.75rem 2.5rem 0rem 8.375rem;
  max-width: 100%;
  width: 100%;
}

.main-nav {
  position: relative;
  gap: 0.8rem;
  display: flex;
  align-items: center;
  min-height: auto;
}
.main-nav__logo {
  margin-right: auto;
  padding-right: 0.625rem;
}
.main-nav__logo img {
  min-width: 36.82px;
  min-height: 36.97px;
}
.main-nav__logo:hover {
  opacity: 0.6;
}
.main-nav__wrapper {
  position: absolute;
  border-radius: 14px;
  padding: 1.25rem 2.5rem;
  right: 0;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  column-gap: 2.1875rem;
  row-gap: 1.25rem;
}
.main-nav__contacts {
  display: flex;
  align-items: center;
  width: 391px;
}
.main-nav__contacts #language {
  font-family: "SF Pro Display", sans-serif;
  cursor: pointer;
  border: none;
  background-color: transparent;
  color: #8B9FCC;
  appearance: none;
  margin-right: auto;
  width: min-content;
}
.main-nav__contacts a {
  display: block;
  text-decoration: none;
  color: white;
  min-width: max-content;
  margin-right: 1rem;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  border-radius: 15px;
  padding-right: 4.0625rem;
  min-width: 30%;
}
.site-list__item a {
  text-decoration: none;
  color: inherit;
  font-family: "SF Pro Text", sans-serif;
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 500;
  color: #5F6B77;
}
.site-list__item a:hover {
  opacity: 0.6;
}

.promo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.promo__wrapper {
  position: relative;
  min-width: 18%;
  max-width: 620px;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 2.8125rem;
  padding: 0.8rem 0;
  z-index: 1;
}
.promo .heading {
  font-size: 3rem;
  line-height: 3.625rem;
  font-weight: 600;
  color: black;
  display: inline;
  border-radius: 15px;
  background: rgba(245, 249, 255, 0.8);
}
.promo__description {
  font-size: 20px;
  line-height: 28px;
  font-weight: 400;
  color: black;
  display: inline;
  padding-top: 0.9375rem;
  margin-right: 4.375rem;
  border-radius: 15px;
  background-color: rgba(245, 249, 255, 0.8);
}
.promo__button {
  font-size: 1rem;
  line-height: 1.375rem;
  font-weight: 500;
  color: white;
  margin-top: 0.875rem;
  width: 170px;
  height: 54px;
}
.promo__button-down {
  width: 54px;
  height: 54px;
  position: relative;
}
.promo__button-down svg {
  position: absolute;
  top: 19px;
  right: 17px;
}

.modal {
  position: absolute;
  z-index: 10;
  top: 20%;
  left: 30%;
  width: 557px;
  height: 519px;
  padding: 2.375rem 2.3125rem;
  border-radius: 11px;
  background-color: white;
  box-shadow: 10px 10px 10px 10px rgba(95, 107, 119, 0.2);
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 600;
  color: black;
  display: none;
}
.modal__title {
  text-align: center;
  font-size: 1.25rem;
  line-height: 1.375rem;
  font-weight: 600;
  color: #08123A;
  padding-bottom: 1.875rem;
}
.modal span {
  font-size: 1rem;
  line-height: 1.25rem;
  font-weight: 600;
  color: #5F6B77;
}
.modal__contacts {
  margin: 0;
  padding: 0;
  list-style: none;
}
.modal__social-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
.modal-show {
  display: block;
  animation: bounce 0.6s;
}

.contacts-list {
  display: flex;
  flex-direction: column;
  gap: 40px;
  padding-bottom: 1.6875rem;
}
.contacts-list__location {
  padding-bottom: 0.8125rem;
  display: flex;
  align-items: center;
  gap: 0.1875rem;
}
.contacts-list__map-link {
  width: fit-content;
  display: block;
  padding-top: 0.4375rem;
  color: #506CCF;
  text-decoration: none;
  border-bottom: 1px solid #506CCF;
}
.contacts-list__item:nth-child(2) {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.contacts-list__contacts-wrapper {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.contacts-list__contacts-wrapper a {
  text-decoration: none;
  color: #08123A;
  padding-left: 0.4375rem;
}
.contacts-list__button {
  width: 188px;
  color: #5F6B77;
}
.contacts-list__work-time {
  padding-bottom: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.4375rem;
}
.contacts-list a:hover {
  opacity: 0.6;
}

.social-list {
  margin: 0;
  padding: 0;
  list-style: none;
  padding-top: 0.75rem;
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 1.25rem;
}
.social-list__item {
  width: 40px;
  height: 40px;
  padding: 0;
  position: relative;
}
.social-list__item:nth-child(3) .button {
  border: none;
}
.social-list__link {
  display: block;
  max-width: 100%;
  width: 40px;
  height: 40px;
  padding: 0.1875rem;
}
.social-list__image {
  position: absolute;
  top: 9px;
  right: 9px;
}
.social-list__image--zen {
  width: 22px;
  height: 22px;
  top: 9px;
  right: 9px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../../img/yandex-zen.png");
}
.social-list a:hover {
  opacity: 0.6;
}